.tabs{

    &__title{
     font-size: 2.4rem;  
     font-weight: 400;   
     margin: 30px 0; 
     letter-spacing: 3px;
    }
    &__text{
        & p + p {
            padding: 40px 0;
        }
        font-weight: 300;
        color: $primary-color;
    }


    &__image {
        max-width: 570px;
        width: 100%;
        @media screen and (max-width:575px){
           margin-top: 20px;
        }
        
    }
    &__img-wrap{
        position: relative;
        margin-right: -100px;
        padding-top: 60%;
        overflow: hidden;
        @media screen and (max-width:770px){
            margin-right: 0;
        }
    }
    &__img{
        @include posA;
        object-fit: cover;
        min-height: 100%;
        min-width: 100%;
    }
}
.nav-tabs{
   margin:112px 0 80px 0 ;
   border-width: 2px;
   border-color: rgba($color: $primary-color, $alpha:.8);
}
.tabs__img-wrap{
    margin-bottom: 30px;
}
.tab-pane{
    min-height: 660px;
}
.nav-tabs .nav-link{
    margin: -2px;
    color: $body-color;
    font-size: 3rem;
    border:0;
    border-bottom:1px solid transparent ;
    border-width: 2px;
    &.active {
        border-color: $black;
    }  
    @include hover{
        color: $primary-color;
        border-color: $black;
    }
   
}
.nav-item + .nav-item{
    .nav-link{
        padding-right:  3.5rem;
        padding-left:  3.5rem;
        
    }
}
.nav-link--secondary{
    @media screen and (max-width:770px){
        font-size: 2rem!important;
    }
    @media screen and (max-width:480px){
        font-size: 1.6rem!important;
    }
    @media screen and (max-width:430px){
        font-size: 1.4rem!important;
        padding-right:  1.5rem!important;
        padding-left:  1.5rem!important;
    }

}