.header{
    position: absolute;
    top: 0; left: 0; right: 0 ;
    z-index: 1;
    padding:0 20px 0;
    border-bottom: $border-width solid rgba($color: $darkness-white, $alpha: .7);
    @media screen and (max-width:580px) {
      padding:0 10px 0;
    }
  &__logo{
    @include tr();
    font-size: 3rem;
    color: $secondary-color;
    text-decoration: none;
  }
  &__form{
    position: relative;
    padding: 22px 15px 22px ;

    @media screen and (max-width:380px) {
      padding: 22px 5px 22px ;
    }
      & svg {
        fill: $secondary-color;
        position: absolute;
        left: 22px; top: 26px;
        @media screen and (max-width:380px) {
         display: none;
        }
      }
  }
  &__search{
    background-color: transparent;
    border: none;
    color: $secondary-color ;
    outline: none;
    max-width: 120px;
    width: 100%;
    padding: 0;
    padding-left: 30px;
    font-size: 1.6rem;
    @media screen and (max-width:380px) {
      padding-left: 0;
     }
    &::placeholder{
      text-transform: uppercase;
      color: $secondary-color;
      text-align: center;
      font-size: 1.6rem;
    }
  }
 
}

.nav {
  &-mob{
    
    @media screen and (max-width:992px){
      transform: scale(0);
      opacity: 0;
      position: fixed;
      @include tr();
      top: 0; left: 0;
     
      .opened & {
        transform: scale(1);
        opacity: 1;
        @include tr();
      }
      
      background-color: rgba($color: $primary-color, $alpha: .7);
      
      &__links{
        height: 100vh;
        width: 100vw;
        display: flex;
        flex-direction: column;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        text-align: center;
      }


     
    }
 
    &__dis {
      @media screen and (max-width:580px){
        display: none;
      }
    }
  }
}

.opened{
  position: fixed;
}
 .nav-link{
  padding: 25px 20px 22px;
  color: $secondary-color;
  font-weight: 400;
  font-size: 1.6rem;
  position: relative;
  @media screen and (max-width:580px) {
    padding: 25px 5px 22px;

    padding-right: 10px!important;
    padding-left: 10px!important;
  }
  &--primary{
    padding-right: 20px!important;
    padding-left: 20px!important;

  }
  @media screen and (min-width:992px){
    &.l-trough {
      &::before{
        content: '';
        @include posA($top:50%,$left:50%,$delta_x:-50%,$delta_y:-50%);
        width: 50px;
        height: 2px;
        background-color: #9db892;
      }
    
  }
  }
     
}
.mobile-icons{
  display: none;
  @media screen and (max-width:580px) {
    display:block;
  }
}
.nav--bordered{
  
    .nav-item + .nav-item {
       border-left:$border-width solid rgba($color: $darkness-white, $alpha: .7);
     }
  
}
.mobileWrap{
  display: none;
  padding: 20px 10px 20px 10px;
  @media screen and (max-width:992px){
    display: block;
    
}
}
.mobileMenu{
  
  z-index: 10;
  
 
 
& span{
    position: relative;
    width: 35px;
    height: 5px;
    background: $secondary-color;
    display: block;
    transition: .5s;
    margin-bottom: 8px;

       &:nth-child(1){
          transform-origin: left;
      }
       &:nth-child(2){
          transform-origin: center;
      }
       &:nth-child(3){
          transform-origin: left;
      }
      .opened  & {
          background:#fff;
          border-radius: 50%;
      }
      .opened &:nth-child(1){
         transform: rotate(45deg);
         top: 2.5px;
         left: 2px;
     
      }
      .opened &:nth-child(2){
         transform: rotate(315deg);
         top: 1px;
         right:3px;
        
      }
      .opened &:nth-child(3){
          transform: scaleX(0);
      }
}
}