.accordion{
min-height: 450px;
}

.card{
    border: 2px solid #eaedde;
    border-right: 0;
    border-left: 0;
    &-header{
        padding-left: 0;
        background: transparent;
        border-bottom: 0;
    
        & .btn {
            padding-left: 0;
            display: flex;
            align-items: center;
            font-size: 1.8rem;
            color: $primary-color;

            
        }
    }
}
.collapse-plus{
    margin-right: 25px;
    fill: $primary-color;
}