.head {
    &--title {
    
    color: $darkness-white;
    font-size: 5.5rem;
    font-weight: 400;
    letter-spacing: 10px;
    @media screen and (max-width:500px){
        font-size: 3.5rem;
    }
  }
  &--subtitle{
      color: rgba($color:  $darkness-white, $alpha: .6);
      font-weight: 300;
      font-size: 2.4rem;
      margin: 30px 0 50px 0;
      letter-spacing: 5px;
  }
  &--link{
      color: rgba($color:  $darkness-white, $alpha: .8);
      font-weight: 300;
      font-size: 1.8rem;
      text-decoration: none;
      letter-spacing:5px;
      @include tr();
  }      
  
  
  }

.vertical-sign{
    position: absolute;
    bottom: 165px;
   
    color: $secondary-color;
    transform-origin: 90px;
    font-size: 2.6rem;
    letter-spacing: 2px;
    transform: rotate(270deg);
   
    & a{
        text-decoration: none;
        color:$secondary-color ; 
        @include tr();
        cursor: pointer;
    }   
    & a::before{
        content: '';
        position: absolute;
        width: 10rem;
        background-color: $secondary-color;
        height: 2px; 
        @include posA($top:50%,$left:-60px,$delta_x:-50%,$delta_y:-50%) ;
        @media screen and (max-width:500px){
            width:0; height: 0;
        } 
    }
    @include hover {
       & a {
           @include tr();
        color: $primary-color;
       }
    }

    @media screen and (max-width:500px){
        left: -40px;
        bottom: 60px;
        font-size: 1.6rem;
    }
}
