.form-control{
    padding: 20px 30px 20px;
    background-color: transparent;
    border: 2px solid rgba($color: $primary-color, $alpha: .6);
    border-right: 0;
    font-size: 1.8rem;
    &:focus{
        border-color:rgba($color: $primary-color, $alpha: .6);
        box-shadow: none;
        background-color: transparent;
    }
    &::placeholder{
        font-size: 1.6rem;
        font-weight: 300;
        color: $primary-color;
    }
}
.bi-arrow-form{
    fill: rgba($color: $primary-color, $alpha: .8);
}