.footer{
    padding: 120px 25px 50px;
    background-color: $darkness-white;

    &__title{
        font-size: 6rem;
        color: $primary-color;
        font-weight: 400;
        position: relative;

        &::before,
        &::after{
            content: '';
            @include posA($top:50%,$left:50%,$delta_x:-50%,$delta_y:-50%);
            width: 375px;
            height: 2px;
            background-color:$primary-color ;
            @media screen and (max-width:992px) {
                width: 200px;
            }
            @media screen and (max-width:540px){
                top: auto;
                bottom: 0;
                width: 150px;
                
            }
        }

        @media screen and (max-width:540px){
           
            &::before{
                width: 0;
                height: 0;
            }
           
        }


        &::before {
            left:220px;

            @media screen and (max-width:992px) {
                left:25%;
            }
            @media screen and (max-width:770px){
                left:20%;
            }
            @media screen and (max-width:575px){
                left:15%;
            }
           
        }
        &::after{
            left: auto;
            right: -160px;
            @media screen and (max-width:992px) {
                right:-3%;
            }
            @media screen and (max-width:770px){
                right:-18%;
            }
            @media screen and (max-width:575px){
                right:-25%;
            }
          
        }
        @media screen and (max-width:540px){
            &::after{
                @include posA($top:auto,$left:50%,$delta_x:-50%,$delta_y:-50%); 
                bottom: 0;
            }
        }
        margin-bottom: 60px;
    }

    &__list {
        list-style: none;
        padding: 0;
        margin: 0;

        &--social{
            display: flex;
            justify-content: space-between;
            position: relative;
            &::after{
                content: '';
                @include posA($top:50%,$left:50%,$delta_x:-50%,$delta_y:-50%);
                position: absolute;
                top:80px;
                left: 50%;
                
                transform: translate(-50%,-50%)  rotate(90deg);
                width: 55px;
                height: 1px;
                background-color:  $primary-color;
            }
        }
        }
    &__description{
        text-align: center;
    }

    &__link{
        font-size: 1.4rem;
        
        color: $primary-color;
        text-transform: uppercase;
        text-decoration: none;

        &--social{
            fill:$primary-color ;

            @include hover{
                transform: scale(1.3);
            }
        }
    }

    &__copyright{
        margin-top: 50px;
        font-size: 1.1rem;
        font-weight: 300;
        color: $primary-color;
    }
}