.quote-card {
    overflow: hidden;
    position: relative;
    width: 100vw;
    min-height: 70vh;
    display: flex;
        justify-content: center;
        flex-direction: column;
        align-content: center;

    &_sign{
        position: absolute;
        bottom: 120px;
        
        font-size: 2.4rem;
        z-index: 1;
        color: $darkness-white;
        font-weight: 300;
        letter-spacing: 3px;
        transform: rotate(90deg);
        
        @media screen and (max-width:640px){
            font-size: 1.8rem;
            left: -40px;
            bottom: 80px;
        }
        @media screen and (max-width:340px){
            font-size: 1.4rem;
            bottom:auto;
            top: 10px;
            left: 50%;
            transform: rotate(0) translate(-50%,-50%);
        }
    }


    &_wrap {
        z-index: -1;
        &::before{
            content: "";
            position: absolute;
            top: 0;    left: 0;
            bottom: 0; right: 0;
            background-color: rgba($color: #000000, $alpha: .5);
        }   
        position: absolute;
        top: 0;left: 0;
        bottom: 0;
        right: 0;
       
    }

    &_img {
       width: 100%;
       height: 100%;

    }
    &_author{
        font-size: 2.4rem;
        color: $secondary-color;
        font-weight: 400;
        position: relative;
        @media screen and (max-width:340px){
            font-size: 1.4rem;
        }
        &::before{
          content: '';
          @include posA($top:50%,$left:auto,$delta_x:-50%,$delta_y:-50%);
          left: -40px;
          width: 4.5rem;
          background-color: $secondary-color;
          height: 2px;  
          @media screen and (max-width:340px){
            height: 1px;
            width: 2.5rem;
            left: -20px;
        }
        }
    }
}
.blockquote{
    color: $secondary-color;
    font-weight: 300;
    font-size: 2.2rem;
    @media screen and (max-width:340px){
        font-size: 1.4rem;
    }
}