.btn{
    border-radius: 0;
    outline: none;
    position: relative;
    &:focus{
        box-shadow: none;
    }
}
.btn-lg{
    font-size: 2rem;
    padding: 20px 80px 20px 30px;
    line-height: 1.3;
}
.btn-outline-info{
    border: $border-width solid rgba($color:  $darkness-white, $alpha: .6);
    color: $secondary-color;
    font-weight: 400;
   
    & span::after {
        content: '';
        position: absolute;
        width: 4.5rem;
        background-color: $secondary-color;
        height: 2px;
        @include posA($top:50%,$left:auto,$delta_x:-50%,$delta_y:-50%) ;
        right: 0;
        transition:  background-color .4s ease-in;
    }
    @include hover {
        background-color: $primary-color;
        border-color: $primary-color;
        color: rgba($color:  $darkness-white, $alpha: .7);
        & span::after{
            transition:  background-color .4s ease-in;
            background-color: rgba($color:  $darkness-white, $alpha: .7);;
        }
    }
}
.btn--price{
    background-color: $primary-color;
    position: absolute;
    top: 0;
    right: -2px;
    // padding-bottom: 22px;
    border: 2px solid $primary-color ;

    &::after{
        content: '';
        position: absolute;
        width: 4.5rem;
        background-color: $secondary-color;
        height: 2px;
        @include posA($top:50%,$left:auto,$delta_x:-50%,$delta_y:-50%) ;
        right: 0;
        transition:  background-color .4s ease-in;
    }

    @include hover {
        background-color: #f1f1f1  ;
        color:$primary-color;
        &::after{
            transition:  background-color .4s ease-in;
            background-color:$primary-color; 
        }
    }
}

.btn-form {
    padding: 0 30px;
    border: 2px solid rgba($color: $primary-color, $alpha: .6);
    border-left: 0;
    & svg {
        @include tr();
    }
    @include hover {
        & svg{
            @include tr();
            transform: scale(1.2);
        }
    }

    &::before {
        content: '';
        position: absolute;
        top: 50%; left: 0;
        transform: translate(-50%,-50%) rotate(90deg);
        width: 50px;
        height: 2px;
        background-color: rgba($color: $primary-color, $alpha: .6);
       
     
    }
}