.slider-buttons{
text-align: center;
padding: 50px 0 100px;
}
.slider-button-prev,
.slider-button-next
{
outline: none;
display: inline-block;
fill: $primary-color;
@include hover {
    fill: $body-color;
}
}
.slider-container{
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
}


.slider-buttons-secondary {
    @include posA($top:auto,$left:50%,$delta_x:-50%,$delta_y:-50%);
    bottom: -45px;
    z-index: 1;
    border: 1px solid rgba($color: $secondary-color, $alpha: .7);
    border-bottom: 0;
    @media screen and (max-width:440px){
        bottom: 0;
        border-radius: 50%;
        border-bottom: 1px solid rgba($color: $secondary-color, $alpha: .7);
    }

   
}
.slider-button-next-secondary{
    border-left: 1px solid rgba($color: $secondary-color, $alpha: .7);
    @media screen and (max-width:440px){
        border-left:0;
    }
}

.slider-button-next-secondary,
.slider-button-prev-secondary{
   
    display: inline-block;
    padding: 20px 10px 20px;
    font-size: 0;
    @media screen and (max-width:440px){
        padding: 10px 10px 10px;
    }
    & svg {
       
        fill: rgba($color: $secondary-color, $alpha: .8);
        @media screen and (max-width:440px){
            width: 20px;
            height: 20px;
        }
    }

    @include hover {
        & svg {
            fill: $primary-color;
        }
    }
}



