@import "variables";
@import "mixins";
@import "swiper.min";
@import "slider";
@import "collapse";

* {
  box-sizing: border-box;
 }

 html {
  font-size: 10px;
  scroll-behavior: smooth;
  

}

body {
  @include font(
    $ff: $body-ff,
    $fz: 1.8rem,
    $fc: $body-color,
    $fw: 400,
    $lh: 1.6
  );
  background-color: $body-bg;
}

img{
  max-width: 100%;
}

.bg-img {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
.section {
  min-height: 100vh;
  position: relative;
  &--head {
    background-image: url("../img/header.jpg");

    @media screen  and (max-width:760px){
      background-image: url("../img/header_tablet.jpg");

    }
    @media screen  and (max-width:450px){
      background-image: url("../img/header_phone.jpg");

    }
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 80px 0;
  }
  &--wahtWeAre{
      background-color: $secondary-color;
      padding: 100px 0;
  }
  &--featured{
    background-color: $secondary-color;
    padding: 0 0 100px 0;
  }
}

@import 'section_head';
@import 'buttons';

.features{
    padding: 30px 80px;
    border: 30px solid #40c4c8;
    border-image: url('../img/border.png') 30 round ;
    @media screen and (max-width:700px){
      padding: 30px 30px;
    }
    &__title{
        font-size: 2.4rem;
        letter-spacing: 3px;
    }
    &__list{ 
        display: flex;
        align-items: center;
        justify-content: center;
        list-style: none;
        margin: 0;
        padding: 0;
        @media screen and (max-width:460px){
          flex-flow: row wrap;
        }
    }
    &__item{
        margin: 0 15px;
        @media screen and (max-width:460px){
          margin: 10px;
        }
    }
   
}

@import 'section_tabs';

.product{
max-width: 615px;
width: 100%;

    &__featured{
        padding: 40px 10px 70px;
        text-align: center;
        max-height: 430px;
        background-color: $secondary-color;
        border: 2px solid rgba($color: $primary-color, $alpha: .4);
    }
    &__title{
      font-size: 1.8rem;
      letter-spacing: 3px;
      margin: 15px 0 35px;
    }
    &__wrap{
      max-width: 200px;
      max-height:250px;
      overflow: hidden;
    }
    &__img{
      object-fit: cover;
    }
    &__price{
      padding:20px;
      background-color: #f1f1f1;
      border:  2px solid #e1e1e1;
      border-top: 0;
      position: relative;
     
    }
    
}
.rate{
    display: flex;
    list-style: none;
    justify-content: center;
    padding: 0;
    margin: 0;
   
    &__item{
        & + & {
            padding-left: 5px;
        }
        
        & svg {
            fill: $primary-color;
        }
    }
}
.featured-title{
  font-size: 3rem;
  font-weight: 400;
  letter-spacing: 3px;
  margin-bottom: 50px;
}

@import 'quote-card';
@import 'footer';
@import 'form-control';

.scroll-down{
  @include posA($top:auto,$left:50%,$delta_x:-50%,$delta_y:-50%);
  bottom: 20px;

  & svg {
    fill: $secondary-color;

    @media screen and (min-width:999px){
      @include hover {
        transform: scale(1.2);
      }
    } 
  } 
} 
@import 'header';

